import React, { useState, useEffect } from "react";
import { CCard, CCardBody, CCardHeader } from "@coreui/react";
import { DocsLink } from "src/reusable";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import axios from "axios";
import MaterialTable from "material-table";
import Avatar from "@material-ui/core/Avatar";
import Grid from "@material-ui/core/Grid";
import Notification from "../../component/AlertMessageBox";
import ConfirmDialog from "../../component/ConfirmDialog";
import Card from "@material-ui/core/Card";

const BrandButtons = () => {
  const [captinNumber, setcaptinNumber] = useState(0);
  const [captinData, setcaptinData] = useState("");
  const [newbalance, setnewbalance] = useState(0);
  const [notify, setNotify] = useState({
    isOpen: false,
    message: "",
    type: "",
  });
  const [confirmDialog, setConfirmDialog] = useState({
    isOpen: false,
    title: "",
    subTitle: "",
  });
  const apiurl = "https://jetek-tr5el.ondigitalocean.app/admin";
  const authRequest = axios.create({
    baseURL: apiurl,
    headers: {
      "auth-token":
        "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjYwNWZhNDliMjUzNjI1MjliMDMzMTBhMyIsImlhdCI6MTYxNzI3OTI5NSwiZXhwIjoxNjE3MzY1Njk1fQ.vUJVRdhU3sMKhnCrZIE9W-7fIAumUNQJ-N8vOIpP3XY",
    },
  });
  const StyledButton = withStyles({
    root: {
      background: "linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)",
      borderRadius: 3,
      border: 0,
      color: "white",
      height: 48,
      padding: "0 30px",
      boxShadow: "0 3px 5px 2px rgba(255, 105, 135, .3)",
      marginTop: 20,
    },
    label: {
      textTransform: "capitalize",
    },
  })(Button);
  const useStyles = makeStyles((theme) => ({
    root: {
      "& > *": {
        margin: theme.spacing(1),
        width: "25ch",
      },
    },
    paper: {
      position: "absolute",
      width: 400,
      backgroundColor: theme.palette.background.paper,
      border: "2px solid #000",
      boxShadow: theme.shadows[5],
      padding: theme.spacing(2, 4, 3),
    },
  }));
  const validateOrSubmin = () => {
    var body = {
      number: captinNumber,
    };

    // const users = await authRequest.post(`/deleteUser`)

    axios
      .post("https://jetek-tr5el.ondigitalocean.app/admin/getCaptinInfo", body)
      .then((response) => response)
      .then((res) => {
        setcaptinData(res.data);
      })
      .catch((err) => {
        console.log(err);
        setNotify({
          isOpen: true,
          message: "لا يوجد سائق مسجل بهذا الرقم  ",
          type: "error",
        });
      });
  };

  const updateCatpinbalance = () => {
    var body = {
      newBalance: `${newbalance * 1 + captinData.balance}`,
      number: captinNumber,
    };
    // const users = await authRequest.post(`/deleteUser`)

    axios
      .post("https://jetek-tr5el.ondigitalocean.app/admin/updateCaptinBalance", body)
      .then((response) => {
        console.log(response);
        if (response.status == 200) {
          setNotify({
            isOpen: true,
            message: "تم   إضافة الرصيد بنجاح",
            type: "success",
          });
        }
      })
      .catch((err) => console.log(err));
  };
  return (
    <>
      <CCard>
        <CCardHeader>
         البحث عن كابتن
          <DocsLink name="-Button" />
        </CCardHeader>
        <CCardBody>
          <TextField
            required
            id="outlined-basic"
            label="رقم الهاتف"
            type=""
            variant="outlined"
            style={{ width: "100%" }}
            onChange={(event) => setcaptinNumber(event.target.value)}
            helperText="5xxxxxxxx"
          />
          <br />
          <StyledButton
            onClick={() => {
              validateOrSubmin();
            }}
            variant="contained"
            className="insert"
            color="primary"
          >
            بحث
          </StyledButton>
        </CCardBody>
      </CCard>
      <CCardBody>
        {captinData ? (
          <Grid item xs={12}>
            <Grid container spacing={3}>
            <Grid item xs={3}>
              <Avatar alt="Remy Sharp" src={captinData.photo} />
            </Grid>
            <Grid item xs={3}>
              <h1>{captinData.firstName}</h1>
            </Grid>
            <Grid item xs={3}>
              <h1>{captinData.identity}</h1>
            </Grid>
            </Grid>
            <CCardBody>
              <TextField
                required
                id="outlined-basic"
                label="إضافة رصيد"
                type=""
                variant="outlined"
                style={{ width: "100%" }}
                onChange={(event) => setnewbalance(event.target.value)}
                helperText="5xxxxxxxx"
              />
              <br />
              <StyledButton
                onClick={() => {
                  updateCatpinbalance();
                }}
                variant="contained"
                className="insert"
                color="primary"
              >
                إضافة
              </StyledButton>
            </CCardBody>
          </Grid>
        ) : (
          <h3>لا توجد بيانات</h3>
        )}
      </CCardBody>
      <Notification notify={notify} setNotify={setNotify} />
      <ConfirmDialog
        confirmDialog={confirmDialog}
        setConfirmDialog={setConfirmDialog}
      />
    </>
  );
};

export default BrandButtons;
